.logo { display: inline-flex; align-items: center; font-weight: 700; font-size: 20px; line-height: 20px; color: #fff; }

.logo__img svg { width: 31px; height: 47px; fill: var(--current); }

.logo__text { padding-left: 11px; }

.logo__text span { color: var(--current); }

.phones { display: flex; margin: 0 auto; padding-left: 2.2%; }

.phones__item { margin-right: 55px; }

.phones__item:last-child { margin: 0px 0px 0px 0px; }

.phones__link { display: flex; align-items: center; font-weight: normal; font-size: 16px; line-height: 20px; color: #fff; }

