.menu-wrapper.is-active .main-nav-list__item:nth-child(3) { transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.1s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .main-nav-list__item:nth-child(4) { transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.2s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .main-nav-list__item:nth-child(5) { transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.3s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .main-nav-list__item:nth-child(6) { transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.4s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .shedule__item:nth-child(1) { transform: translateY(0); transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.2s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .shedule__item:nth-child(2) { transform: translateY(0); transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.4s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .social-list__item { transform: translateY(0); transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.2s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .social-list__item:nth-child(2) { transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.4s, color 0.3s ease-in-out 0s; }

.menu-wrapper.is-active .social-list__item:nth-child(3) { transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1) 1.6s, color 0.3s ease-in-out 0s; }

.main-nav { width: 0; top: 0; right: 0; height: 100%; position: absolute; display: flex; flex-direction: column; background: #141414; transition: all 0.3s; z-index: 1001; transition: all 2s cubic-bezier(0.19, 1, 0.22, 1) 0.5s; transform: translateZ(0); max-height: 100%; }

.main-nav__title { display: flex; justify-content: space-between; align-items: center; margin-bottom: auto; transform: translateY(-100px); transition: transform 0.7s cubic-bezier(0.78, 0, 0.81, 0) 0s, color 0.3s ease-in-out 0s; }

.main-nav .shedule { margin: 0 0 0; padding-left: 61px; }

.main-nav.active { right: 0; }

.main-nav .social-list { margin-top: 35px; overflow: hidden; }

.main-nav .social-list__item { transform: translateY(30rem); transition: transform 0.7s cubic-bezier(0.78, 0, 0.81, 0) 0s, color 0.3s ease-in-out 0s; }

.main-nav .shedule__item { font-size: 16px; line-height: 20px; }

.main-nav .shedule__item:nth-child(1) { transform: translateX(30rem); transition: transform 0.7s cubic-bezier(0.78, 0, 0.81, 0) 0s, color 0.3s ease-in-out 0s; }

.main-nav .shedule__item:nth-child(2) { transform: translateX(30rem); transition: transform 0.7s cubic-bezier(0.78, 0, 0.81, 0) 0s, color 0.3s ease-in-out 0s; }

.nav-mask { background: rgba(0, 0, 0, 0.9); position: fixed; left: 0; top: 0; width: 100%; height: 100%; z-index: 1000; }

.button-nav-close { font-weight: 500; font-size: 15px; line-height: 20px; letter-spacing: -0.03em; color: #fff; }

.button-nav-close:hover { color: var(--current); }

.main-nav-list { margin: auto 0; padding: 10px 0 10px 61px; display: block !important; }

.main-nav-list__item { margin-bottom: 30px; opacity: 1; transform: translateX(30rem); transition: transform 0.7s cubic-bezier(0.78, 0, 0.81, 0) 0s, color 0.3s ease-in-out 0s; }

.main-nav-list__item:last-child { margin: 0px 0px 0px 0px; }

.main-nav-list__item.active a { color: var(--current); }

.main-nav-list__link { font-weight: 500; font-size: 30px; line-height: 36px; color: #fff; }

.main-nav-list__link:hover { color: var(--current); }

.noscroll { overflow: hidden; }

body.noscroll { margin-right: 10px; }

@media screen and (max-height: 800px) { .menu-wrapper.is-active .main-nav { padding-bottom: 40px; } }

@media screen and (max-height: 700px) { .menu-wrapper.is-active .main-nav { padding-bottom: 40px; }
  .main-nav-list__item { margin-bottom: 15px; } }

