/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

ul { list-style-type: none; }

@keyframes spin { from { transform: rotate(0deg); }
  to { transform: rotate(360deg); } }

@keyframes spin-reverse { from { transform: rotate(360deg); }
  to { transform: rotate(0deg); } }

.load { position: fixed; left: 0; top: 0; width: 100%; height: 100%; }

.load__icon { display: block; position: relative; left: 50%; top: 50%; width: 150px; height: 150px; margin: -75px 0 0 -75px; border-radius: 50%; border: 3px solid transparent; border-top-color: #2dff7d; animation: spin 1.7s linear infinite; z-index: 11; }

.load__icon:before { content: ""; position: absolute; top: 5px; left: 5px; right: 5px; bottom: 5px; border-radius: 50%; border: 3px solid transparent; border-top-color: #ff296a; animation: spin-reverse 0.6s linear infinite; }

.load__icon:after { content: ""; position: absolute; top: 15px; left: 15px; right: 15px; bottom: 15px; border-radius: 50%; border: 3px solid transparent; border-top-color: #ff5c00; animation: spin 1s linear infinite; }

.load__screen { position: fixed; top: 0; width: 51%; height: 100%; background: #141414; z-index: 10; }

.load__screen_left { left: 0; }

.load__screen_right { right: 0; }

.loading .load__screen_left { transform: translateX(-100%); transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.loading .load__screen_right { transform: translateX(100%); transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.loading .load__icon { opacity: 0; transition: all 0.3s ease-out; }

.loading .load { visibility: hidden; transform: translateY(-100%); transition: all 0.3s 1s ease-out; }

/* Loaded styles */
.loaded #loader-wrapper .loader-section.section-left { transform: translateX(-100%); transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.loaded #loader-wrapper .loader-section.section-right { transform: translateX(100%); transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }

.loaded #loader { opacity: 0; transition: all 0.3s ease-out; }

.loaded #loader-wrapper { visibility: hidden; transform: translateY(-100%); transition: all 0.3s 1s ease-out; }

